import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import swal, { SweetAlertOptions } from 'sweetalert2';
import { formatDate } from '@angular/common';

@Component({
	selector: 'app-order-record',
	templateUrl: './order-record.component.html',
	styleUrls: ['./order-record.component.css']
})
export class OrderRecordComponent implements OnInit {

	list;
	start_date;
	end_date = formatDate(new Date(), 'yyyy-MM-dd', 'en-US');
	moredata = 1;
	//是否開啟read more div
	readmoreCheck = 0;
	payUrl = `${Config.APIUrl}/api/shop/payment/go-pay`;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		//搜尋日期為30天內
		let today = new Date().getTime();
		let oneMonth = today - 60*60*24*30*1000;
		this.start_date = formatDate(oneMonth, 'yyyy-MM-dd', 'en-US');
		console.log(this.start_date);
		this.getOrderList();
	}


	//取消訂單
	cancelOrder(id){

		swal({
	    	title: `刪除訂單`,
		  	text: "確定要刪除此訂單? ",
		  	type: "question",
		  	showCancelButton:true,
		  	focusCancel:true,
				
		}).then(
			(success) => {
				console.log(success);
				if(success.value){
					let para = new FormData();
					para.append('id',id);
					para.append('_method',"PUT");
					let url = `${Config.APIUrl}/api/shop/order/cancel-shop-order`;
					this.httpService.postHttp(url, para).subscribe(
						res => {
							swal({
								title: '操作成功!',
								text: '訂單已取消',
								type: 'success',
							}).then(
								success => location.reload()
							);
						}
					);
					
				}
	    });
		
	}


	//取得訂單列表
	getOrderList() {
		let url:string;

		const paramObject = {
			'start_date': this.start_date,
			'end_date': this.end_date,
			'sort_order': 'desc',
			'type': 'shop',
			'from': 0,
			'limit': 50,
		};
		//console.log(paramObject);

		let param = this.httpService.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/shop/order?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.orders.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.orders;

				console.log(res);
			}
		);

	}


	//search
	changeSearch(event){
		console.log(event.target.value);
		//搜尋日期
		let today = new Date().getTime();
		let newday = today - 60*60*24*event.target.value*1000;
		this.start_date = formatDate(newday, 'yyyy-MM-dd', 'en-US');
		console.log(this.start_date);
		this.getOrderList();
	}
}
