import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';

@Component({
	selector: 'app-order-detail',
	templateUrl: './order-detail.component.html',
	styleUrls: ['./order-detail.component.css']
})
export class OrderDetailComponent implements OnInit {

	order;
	fileUrl = Config.FileUrl;
	payUrl = `${Config.APIUrl}/api/shop/payment/go-pay`;
	
	constructor(private router:Router, private route:ActivatedRoute, private httpService:HttpService) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
    		this.getOrder(params['id']);
    	});
	}


	//取得訂單資訊
	getOrder(id){
		let url = `${Config.APIUrl}/api/shop/order/show?id=${id}`;
		this.httpService.getHttp(url).subscribe(
			res => this.order = res
		);
	}

}
