import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginGuard } from '../guards/login.guard';
import { CompleteInfoGuard } from '../guards/complete-info.guard';

//component
import { OrderRecordComponent } from "../member/order-record/order-record.component";
import { OrderDetailComponent } from "../member/order-detail/order-detail.component";
import { MemberHomeComponent } from '../member/member-home/member-home.component';
import { UserinfoComponent } from '../member/userinfo/userinfo.component';
import { ResetPasswordComponent } from '../auth/reset-password/reset-password.component';
import { UsereditComponent } from '../member/useredit/useredit.component';
import { MemberProductComponent } from './member-product/member-product.component';
import { MessageListComponent } from './message-list/message-list.component';
import { MessageInfoComponent } from './message-info/message-info.component';
import { MemberPsychologyComponent } from './member-psychology/member-psychology.component';
import { MemberPsychologyAnswerComponent } from './member-psychology-answer/member-psychology-answer.component';
import { MemberLaterbuyComponent } from './member-laterbuy/member-laterbuy.component';
import { ReviewHomeComponent } from './review-home/review-home.component';
import { MemberFavoriteComponent } from './member-favorite/member-favorite.component';

const routes: Routes = [

	//會員專區
	{ path: '', component: MemberHomeComponent, canActivate:[LoginGuard],
		children: [
			{ path: 'userinfo', component: UserinfoComponent },
			{ path: 'useredit', component: UsereditComponent },
			{ path: 'fillcompleteinfo/:returnUrl', component: UsereditComponent },
			{ path: 'reset-password', component: ResetPasswordComponent },
			{ path: 'order-record', component: OrderRecordComponent },
			{ path: 'order-detail/:id', component: OrderDetailComponent },
			{ path: 'myproduct', component: MemberProductComponent },
			{ path: 'mail', component: MessageListComponent },
			{ path: 'mail/:id', component: MessageInfoComponent },
			{ path: 'mytest', component: MemberPsychologyComponent },
			{ path: 'mytest/:id', component: MemberPsychologyAnswerComponent },
			{ path: 'buylater', component: MemberLaterbuyComponent },
			{ path: 'review', component: ReviewHomeComponent },
			{ path: 'favorite', component: MemberFavoriteComponent },
		]
	},

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MemberRoutingModule { }
