import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-member-product',
	templateUrl: './member-product.component.html',
	styleUrls: ['./member-product.component.css']
})
export class MemberProductComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;

	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 2;
	moredata = 1;

	list;
	input_ebook;
	input_activity;
	input_course;

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;
    	this.getProduct();
	}

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.input_activity = {
			'type':'activity',
			'from':0,
			'limit':this.limit,
		};

		this.input_ebook = {
			'type':'e-book',
			'from':0,
			'limit':this.limit,
		};

		this.input_course = {
			'type':'course',
			'from':0,
			'limit':this.limit,
		};
	}


	//取得會員商品
	getProduct(){
		let param = this.httpService.getParamFormat(this.input_param);
		let url = `${Config.APIUrl}/api/shop/my-product?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.products.length==0){
					console.log("empty array");
					this.moredata = 0;
				}

				this.list = res.products;
			}
		);
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}

}
