import { Component, OnInit } from '@angular/core';
import { MemberService } from '../member.service'
import { HttpService } from '../../services/http.service';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { formatDate } from '@angular/common';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Component({
  	selector: 'app-useredit',
  	templateUrl: './useredit.component.html',
  	styleUrls: ['./useredit.component.css']
})


export class UsereditComponent implements OnInit {

	data;
	submitted = false;
	birthday = null; //ngmodel for date
	avatar = null;
	inputDate = null; //傳給datepicker的日期
	returnUrl;
	img_url = Config.defaultAvatar;
	strFormChild: Date; //<==兒子datepicker的回傳值的顯示屬性
	//傳給FILE UPLOAD的參數
	inputImageUpload = {
		isCheckImageType: true, //是否檢查圖片副檔名 
		isCheckFileSize: true,  //是否檢查圖片檔案
		isCheckVideoType: false,//是否檢查video副檔名 
		isCheckVideoTime: false,//是否檢查video時間 
		width: 300,
	};
	

	userForm = this.formBuilder.group({
		name: ['',Validators.required],
		gender: ['',Validators.required],
		email: ['',[Validators.required, Validators.pattern('^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\\.[a-zA-Z0-9_-]+)+$')]],
		country: [''],
		address: [''],
		birth: [''],
		phone: [''],
		sexual: ['', Validators.required],
		family: ['', Validators.required],
		avatar: [''],
	});

	// convenience getter for easy access to form fields
	get username() { return this.userForm.get('username'); }
	get password() { return this.userForm.get('password'); }
	get password_confirmation() { return this.userForm.get('password_confirmation'); }
	get name() { return this.userForm.get('name'); }
	get birth() { return this.userForm.get('birth'); }
	get gender() { return this.userForm.get('gender'); }
	get email() { return this.userForm.get('email'); }
	get country() { return this.userForm.get('country'); }
	get address() { return this.userForm.get('address'); }
	get phone() { return this.userForm.get('phone'); }
	get sexual() { return this.userForm.get('sexual'); }
	get family() { return this.userForm.get('family'); }

  	constructor(private route:ActivatedRoute, private httpService:HttpService, private memberService:MemberService, private router:Router, private formBuilder: FormBuilder) { }

	ngOnInit() {
		//初始化user info
		this.memberService.getUserInfo().subscribe(data => {
				this.data = data;
				this.birthday = data.birth;
				this.inputDate = data.birth;
				this.avatar = data.avatar;
				if(data.avatar!="")
					this.img_url = `${Config.FileUrl}/${data.avatar}`;
			});

		// get return url from route parameters or default to '/'
		this.route.params.subscribe(params => {
			this.returnUrl = params['returnUrl'] || '/member/userinfo';
			console.log(this.returnUrl);
		});
		
		
	}


	//[datepicker] Child模板事件發生時，會呼叫此方法回傳值
	onListenChild($event: any) {
	    this.strFormChild = $event as Date;
	    this.birthday = formatDate(this.strFormChild, 'yyyy-MM-dd', 'en-US');
	}


	//[fileupload] Child模板事件發生時，會呼叫此方法回傳值
	onListenFile($event: any) {
		console.log($event);
		this.avatar = $event;
		this.img_url = null;
	}


	//submit form
	onSubmit() {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.userForm.invalid){
			return;
		}

  		let params:FormData = new FormData();
  		Object.keys(this.userForm.value).map(key=> params.append(key,this.userForm.value[key]));
  		params.append('_method','PUT');

  		const url = `${Config.APIUrl}/api/user`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {
		    	//update user info
		    	this.memberService.getUser();

			    swal({
					title: '修改成功!',
					text: '個人資料修改成功',
					type: 'success',
				}).then(
					(success)=> {
					this.router.navigateByUrl(this.returnUrl);
			    	//this.router.navigate(['../userinfo'],  { relativeTo: this.route });
				});
				
		    });
	}

}
