import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-member-psychology',
	templateUrl: './member-psychology.component.html',
	styleUrls: ['./member-psychology.component.css']
})
export class MemberPsychologyComponent implements OnInit {


	list;

	constructor(private http:HttpService) { }

	ngOnInit() {
		this.getPsychologyList();
	}


	//取得心理測驗列表
	getPsychologyList() {
		let url:string;

		const paramObject = {
			'from': 0,
			'limit': 9999,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/member/psychological-test-answer?${param}`;
		this.http.getHttp(url).subscribe(
			res => {

				this.list = res.answers;
				console.log(res);
			}
		);

	}

}
