import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-message-info',
	templateUrl: './message-info.component.html',
	styleUrls: ['./message-info.component.css']
})
export class MessageInfoComponent implements OnInit {

	mail;

	constructor(private httpService:HttpService, private route:ActivatedRoute) { }

	ngOnInit() {
		this.route.params.subscribe(
			params => this.getMail(params['id'])
		);
	}


	getMail(id){

		let url = `${Config.APIUrl}/api/member/mail/show?id=${id}`;
		this.httpService.getHttp(url).subscribe(
			res => this.mail = res
		);
	}

}
