import { Component, OnInit } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { formatDate } from '@angular/common';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Component({
	selector: 'app-reset-password',
	templateUrl: './reset-password.component.html',
	styleUrls: ['./reset-password.component.css']
})

export class ResetPasswordComponent implements OnInit {

	submitted = false;
	pw_value = null; //ngmodel for pw
	pw_confirm = null; //ngmodel for pw
	
	registerForm = this.formBuilder.group({
		old_password: ['',[Validators.required, Validators.minLength(6)]],
		password: ['',[Validators.required, Validators.minLength(6)]],
		password_confirmation: ['',Validators.required],
	});

	// convenience getter for easy access to form fields
	get old_password() { return this.registerForm.get('old_password'); }
	get password() { return this.registerForm.get('password'); }
	get password_confirmation() { return this.registerForm.get('password_confirmation'); }

	constructor(private httpService:HttpService, private router:Router, private formBuilder: FormBuilder) { }

  	ngOnInit() {
  	}


  	//submit form
	onSubmit() {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.registerForm.invalid || this.pw_value!=this.pw_confirm){
			return;
		}

  		let params:FormData = new FormData();
  		Object.keys(this.registerForm.value).map(key=> params.append(key,this.registerForm.value[key]));
  		params.append('_method','PUT')
  		const url = `${Config.APIUrl}/api/user/reset-pass`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {
		    	
			    swal({
					title: '重設密碼成功!',
					text: '重設密碼成功!',
					type: 'success',
				}).then(
					(success)=> {
			    	this.router.navigate(['/home']);
				});
				
		    });
	}


}
