import { Component, OnInit } from '@angular/core';
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';

@Component({
	selector: 'app-review-commentted',
	templateUrl: './review-commentted.component.html',
	styleUrls: ['./review-commentted.component.scss']
})
export class ReviewCommenttedComponent implements OnInit {

	from = 0;
	limit = 999;
	list;
	fileUrl = Config.FileUrl;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.getList();
	}

	//取得未評價清單
	getList(){
		let url = `${Config.APIUrl}/api/shop/product-review?from=${this.from}&limit=${this.limit}&status=1`;
		this.httpService.getHttp(url).subscribe(
			res => {
				if(res.reviews.length>0)
					this.list = res.reviews;
			}
		);
	}

}
