import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Component({
	selector: 'app-review-uncomment',
	templateUrl: './review-uncomment.component.html',
	styleUrls: ['./review-uncomment.component.scss']
})
export class ReviewUncommentComponent implements OnInit {

	from = 0;
	limit = 999;
	list;
	fileUrl = Config.FileUrl;

	Form = this.formBuilder.group({
		score: ['',Validators.required],
		message: ['',Validators.required],
	});
	submitted;

	// convenience getter for easy access to form fields
	get score() { return this.Form.get('score'); }
	get message() { return this.Form.get('message'); }

	constructor(private httpService:HttpService, private formBuilder: FormBuilder) { }

	ngOnInit() {
		this.getList();
	}


	//取得未評價清單
	getList(){
		let url = `${Config.APIUrl}/api/shop/product-review?from=${this.from}&limit=${this.limit}&status=0`;
		this.httpService.getHttp(url).subscribe(
			res => {
				if(res.reviews.length>0)
					this.list = res.reviews;
			}
		);
	}


	//submit form
	onSubmit(id) {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.Form.invalid){
			return;
		}

  		let params:FormData = new FormData();
  		Object.keys(this.Form.value).map(key=> params.append(key,this.Form.value[key]));
  		params.append('id',id);
  		//params.append('_method','POST');

  		const url = `${Config.APIUrl}/api/shop/product-review`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {

			    swal({
					title: '成功!',
					text: '評價已送出',
					type: 'success',
				}).then(
					(success)=> {
			    	location.reload();
				});
				
		    });
	}


	//刪除評價留言
	del(id){
		const url = `${Config.APIUrl}/api/shop/product-review?id=${id}`;

	  	this.httpService.deleteHttp(url)
		    .subscribe(res => {

			    swal({
					title: '成功!',
					text: '評價已刪除',
					type: 'success',
				}).then(
					(success)=> {
			    	location.reload();
				});
				
		    });
	}

}
