import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { formatDate } from '@angular/common';
import swal, { SweetAlertOptions } from 'sweetalert2';
declare var jquery:any;
declare var $ :any;

@Component({
	selector: 'app-message-list',
	templateUrl: './message-list.component.html',
	styleUrls: ['./message-list.component.css']
})
export class MessageListComponent implements OnInit {

	list;
	del_ids = {};

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.getMails();
	}


	//獲得信件列表
	getMails(){
		let sixmonth = new Date().getTime() - 60*60*24*180*1000;
		let paramObject = {
			'start_date': formatDate(sixmonth, 'yyyy-MM-dd', 'en-US'),
			'end_date': formatDate(new Date(), 'yyyy-MM-dd', 'en-US'),
			'from':0,
			'limit':999
		};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/member/mail?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => this.list = res.mails
		);
	}


	//全選
	selectAll(event){
		/* Selected */
		if(event.target.checked){
			$(".delcheck").prop('checked', true);
			this.del_ids = {};
			Object.keys(this.list).map(key=> this.del_ids[this.list[key].id] = this.list[key].id);
		}
		else{
			$(".delcheck").prop('checked', false);
			this.del_ids = {};
		}

		console.log(this.del_ids);
	}


	//勾選刪除方塊
	checkDel(event){

		/* Selected */
		if(event.target.checked){
			this.del_ids[event.target.id] = event.target.id;

		//unselect
		}else{
			delete this.del_ids[event.target.id];
		}

		console.log(this.del_ids);
	}


	//刪除信件
	delMail(){
		let param = JSON.stringify(this.del_ids);
		let url = `${Config.APIUrl}/api/member/mail?ids=${param}`;
		this.httpService.deleteHttp(url).subscribe(
			res => {
				swal({
					title: '刪除成功!',
					text: '選擇的訊息已刪除',
					type: 'success',
				}).then(
					(success)=> {
			    	location.reload();
				});
			}
		);
	}


}
