import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para


@Component({
	selector: 'app-member-psychology-answer',
	templateUrl: './member-psychology-answer.component.html',
	styleUrls: ['./member-psychology-answer.component.css']
})
export class MemberPsychologyAnswerComponent implements OnInit {

	data;
	test_id;

	constructor(private httpService:HttpService, private route:ActivatedRoute) { }

	ngOnInit() {
		this.route.params.subscribe(
			param => {
				this.getPsychologyAnswer(param['id']);
				this.test_id = param['id'];
			}
		);
	}


	//取得心理測驗答案
	getPsychologyAnswer(id) {
		let url:string;


		url = `${Config.APIUrl}/api/member/psychological-test-answer/answers?id=${id}`;
		this.httpService.getHttp(url).subscribe(
			res => {

				this.data = res;
				console.log(res);
			}
		);

	}

}
