import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-member-psychology-hashtag',
	templateUrl: './member-psychology-hashtag.component.html',
	styleUrls: ['./member-psychology-hashtag.component.scss']
})
export class MemberPsychologyHashtagComponent implements OnInit {

	@Input() input_test_ans_id;
	@Input() input_hashtags;
	name;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		
	}


	//檢查hashtag是否重複
	check(name){
		let check_id = this.input_hashtags.findIndex(hashtag => hashtag.name === name); 
		console.log(check_id);
		return check_id;
	}



	//新增hashtag
	add(){
		let check_id = this.check(this.name);

		if(this.name && check_id==-1){
			let url = `${Config.APIUrl}/api/content/hashtag/p_q_answer`;
			let params:FormData = new FormData();
			params.append('id', this.input_test_ans_id);
			params.append('name', this.name);
			
			this.httpService.postHttp(url, params).subscribe(
				res => this.input_hashtags.push({
							'id': res.id,
							'name': this.name
						})
			);
		}
		console.log(this.input_hashtags);
	}


	//移除hashtag
	remove(hashtag_id){
		let url = `${Config.APIUrl}/api/content/hashtag/p_q_answer?id=${this.input_test_ans_id}&hash_tag_id=${hashtag_id}`;

		this.httpService.deleteHttp(url).subscribe(
			res => {
				const index = this.input_hashtags.findIndex(hashtag => hashtag.id === hashtag_id); 
				this.input_hashtags.splice(index, 1);
			}
		);

	}

}
