import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import swal, { SweetAlertOptions } from 'sweetalert2';
import { ProductService } from "../../services/product.service";

@Component({
	selector: 'app-member-laterbuy',
	templateUrl: './member-laterbuy.component.html',
	styleUrls: ['./member-laterbuy.component.css']
})


export class MemberLaterbuyComponent implements OnInit {

	list;
	del_ids = {};
	move_ids = {};
	fileUrl = Config.FileUrl;

	constructor(private httpService:HttpService, private productService:ProductService) { }

	ngOnInit() {
		this.getList();
	}


	//取得下次再買清單
	getList(){
		let url = `${Config.APIUrl}/api/shop/later-buy`;
		this.httpService.getHttp(url).subscribe(
			res => {
				if(res.products.length>0) 
					this.list = res.products;
				console.log(res.products);
			}
		);
	}


	//全選
	selectAll(event){
		/* Selected */
		if(event.target.checked){
			$(".delcheck").prop('checked', true);
			this.del_ids = {};
			this.move_ids = {};
			Object.keys(this.list).map(
				key=> {
					this.del_ids[this.list[key].id] = this.list[key].id;
					this.move_ids[this.list[key].id] = this.list[key].quantity;
				}
			);
		}
		else{
			$(".delcheck").prop('checked', false);
			this.del_ids = {};
			this.move_ids = {};
		}

		console.log(this.del_ids);
	}


	//勾選刪除方塊
	checkDel(event, pNum){

		/* Selected */
		if(event.target.checked){
			this.del_ids[event.target.id] = event.target.id;
			this.move_ids[event.target.id] = pNum;

		//unselect
		}else{
			delete this.del_ids[event.target.id];
			delete this.move_ids[event.target.id];
		}

		console.log(this.del_ids);
		console.log(this.move_ids);
	}


	//刪除商品
	delProduct(del_id){

		if(del_id!="all"){
			this.del_ids = {};
			this.del_ids[del_id] = del_id;
		}

		let param = JSON.stringify(this.del_ids);
		let url = `${Config.APIUrl}/api/shop/later-buy/products?products=${param}`;
		this.httpService.deleteHttp(url).subscribe(
			res => {
				swal({
					title: '刪除成功!',
					text: '選擇的商品已從下次購買清單中刪除',
					type: 'success',
				}).then(
					(success)=> {
			    	location.reload();
				});
			}
		);
	}


	//加入購物車
	moveCart(move_id, move_num){



		//將商品加入購物車
		if(move_id!="all"){
			this.move_ids = {};
			this.move_ids[move_id] = move_num;
		}

		let params:FormData = new FormData();
		params.append('products', JSON.stringify(this.move_ids));
		let url = `${Config.APIUrl}/api/shop/cart/merge`;
		this.httpService.postHttp(url, params).subscribe(
			res => {
				//更新購物車數量
				this.productService.getCartNumber();
				swal({
					title: '操作成功!',
					text: '加入購物車成功',
					type: 'success',
				}).then(
					(success)=> {
					//將商品從下次再買清單移除
					this.delProduct(move_id);
				});
			}
		);
	
	}
	
}
