import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import { MemberService } from '../member.service'

import  * as Config from '../../models/config.model';

@Component({
  selector: 'app-userinfo',
  templateUrl: './userinfo.component.html',
  styleUrls: ['./userinfo.component.css']
})


export class UserinfoComponent implements OnInit {

	data;
	imgurl;

  	constructor(private httpService:HttpService, private memberService:MemberService) { }

	ngOnInit() {
		this.memberService.getUserInfo()
			.subscribe(data => { 
				this.data = data; 
				if(data.avatar=="")
					this.imgurl = Config.defaultAvatar;
				else
					this.imgurl = `${Config.FileUrl}/${data.avatar}`;
			});
	}

	



}
